.slider {
	&__header {
		color: $white;
		padding: 42px 0;
		justify-content: center;
		@media (max-width: 479.98px) {
			padding: 16px;
			justify-content: space-between;
		}
	}
	.slide {
		a {
			outline: none;
		}
	}
	&__title {
		text-transform: uppercase;
		font-size: 32px;
		line-height: 36px;
		font-weight: bold;
		letter-spacing: 0.05em;
		margin-bottom: 0;
		padding: 0 100px;
		@media (max-width: 479.98px) {
			padding: 0;
		}

	}
	&__btn {
		background: none;
		border: 2px solid $white;
		border-radius: 50%;
		width: 50px;
		height: 50px;
		color: $white;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		i {
			font-size: 20px;
		}
	}
}

.slick-dots {
	display: flex;
	justify-content: center;
	list-style: none;
	padding: 0;
	margin: 0;
	li {
		padding: 10px;
		button {
			outline: none;
			box-shadow: none;
		}
		&.slick-active {
			button {
				border-color: $green-accent;
				background-color: $green-accent;
			}
		}
	}
	button {
		background-color: transparent;
		border: 2px solid $black;
		height: 16px;
		width: 16px;
		font-size: 0;
		border-radius: 50%;
	}
}