.order-complete {
	padding-left: 76px;
	@media (max-width: 991.98px) {
		padding-left: 0;
		.text-lg {
			font-size: 24px;
		}
	}
	&-section {
		padding-top: 60px;
		padding-bottom: 75px;
		@media (max-width: 991.98px) {
			padding: 40px 0;
		}
	}
	&-text {
		max-width: 500px;
		font-size: 18px;
		line-height: 27px;
		margin-bottom: 31px;
		color: $black;
	}
	.order-card {
		box-shadow: 0px 30px 60px rgba(0, 0, 0, 0.2);
		background-color: $white;
		padding: 40px;
		margin-left: 30px;
		@media (max-width: 1440px) {
			margin-left: 0;
		}
		@media (max-width: 991.98px) {
			box-shadow: none;
			padding: 0;
		}
		.btn {
			margin-top: 22px;
			width: 100%;
			background-color: $green-accent;
			color: $white;
		}
	}
	.card-title {
		font-size: 18px;
		margin-bottom: 26px;
		color: $black;
	}
	.card-list {
		li {
			margin-bottom: 5px;
		}
		&__title {
			flex: 0 0 140px;
			font-size: 18px;
			line-height: 36px;
			margin-right: 34px;
		}
		&__text {
			font-size: 18px;
			line-height: 36px;
		}
	}
}